<template>
  <section class="uk-section uk-container uk-container-small uk-width-xlarge">

    <h1>Sessions</h1>
    <ul v-for="session in sessions" :key="session.id">
      <li><router-link :to="'/sessions/' + session.id">{{ session.title }}</router-link></li>
    </ul>

  </section>
</template>

<script>

export default {
	name: 'sessions',
	data() {
		return {
			sessions: [],
      apiUrl: process.env.VUE_APP_CRAFT_API_BASE_URL,
		};
	},
	created() {
		// Call method getData when mounted
		this.fetchData();
		// Call method getData every minute
		setInterval(() => {
			this.getData();
			console.log('Data fetched (every minute)');
		}, 360000);
	},
	methods: {
		fetchData() {
			// When online, fetch from api
			if (navigator.onLine) {
				this.quotes = JSON.parse(localStorage.getItem('sessions'));
				console.log('Online : data fetched from localStorage');
				this.getSessions();
			} else {
				// When offline, fetch from cache
				this.quotes = JSON.parse(localStorage.getItem('sessions'));
				console.log('Offline : data fetched from localStorage');
			}
		},
		getSessions() {
			/* eslint-disable */
      this.$http.get(`${this.apiUrl}sessions.json`)
      .then(response => {
        this.sessions = response.data.data;
        console.log('Sessions fetched from API');
        localStorage.setItem('sessions', JSON.stringify(response.data.data));
        console.log('Sessions send to localStorage');
      }, response => {
        // error callback
      });
    }
  },
  computed: {},
};
</script>

