<template>
  <section class="uk-section uk-section-xsmall uk-container uk-container-small uk-width-xlarge">

    <section class="uk-section uk-section-xsmall">
      <h1 class="uk-h2">{{ group.title }}</h1>
      <p>{{ group.groupDescription }}</p>
    </section>

    <section class="uk-section uk-section-xsmall">
      <h2 class="uk-h2">Prochaine session</h2>
      <div v-if="group.nextSession" class="uk-card uk-card-body uk-card-small uk-card-primary"><router-link :to="'/sessions/' + group.nextSession.id">{{ group.nextSession.schedule.date| moment().locale('fr').format("dddd DD")|capitalize }}  {{ group.nextSession.schedule.date| moment().format("HH:mm") }}</router-link></div>
      <p v-else>Pas de sessions programmes pour le moment</p>

      <h2 class="uk-h2">Prochaines sessions</h2>
      <li v-for="session in group.sessions" v-if="group.sessions.length"><router-link :to="'/sessions/' + session.id">{{ session.schedule.date| moment().locale('fr').format("dddd DD")|capitalize }}  {{ session.schedule.date| moment().format("HH:mm") }}</router-link></li>
      <p v-else>Pas de sessions programmes pour le moment</p>

    </section>

  </section>
</template>

<script>
export default {
	name: 'Group',
	data() {
		return {
			group: [],
      apiUrl: process.env.VUE_APP_CRAFT_API_BASE_URL,
		};
	},
	computed: {
		groupId() {
			return this.$route.params.id;
		},
	},
	mounted() {
		if (navigator.onLine) {
			// When online, fetch data and set to localStorage
			this.$Progress.start();
			fetch(`${this.apiUrl}groups/${this.groupId}.json`)
				.then((res) => {
					if (res.ok) return res.json();
					throw new Error('api data preload failed');
				})
				.then((res) => {
					this.group = res;
					localStorage.setItem(`group-${this.groupId}`, JSON.stringify(res));
					console.log('Online : group sended to localStorage');
					this.$Progress.finish();
				})
				.catch(e => console.error(e.message));
		} else {
			// When offline, fetch from localStorage
			this.group = JSON.parse(localStorage.getItem(`group-${this.groupId}`));
			console.log('Offline : group fetched from localStorage');
		}
	},
	method: {
		goBack() {
			window.history.back();
		},
	},
};
</script>

