<template>
  <section class="uk-section uk-section-xsmall uk-container uk-container-small uk-width-xlarge">

	<section class="uk-section uk-section-small">
		<h1 class="uk-h2">{{ exercice.title }}</h1>

		<img v-if="exercice.illustration != null" :src="exercice.illustration.url" :alt="exercice.illustration.title">
    <vue-markdown v-if="exercice.exerciceDescription" :source="exercice.exerciceDescription"></vue-markdown>

    <dl class="uk-tile uk-tile-small uk-tile-muted">
			<dt v-if="exercice.note">Note</dt>
			<dd v-if="exercice.note"><vue-markdown>{{ exercice.note }}</vue-markdown></dd>
			<dt v-if="exercice.difficulty">Difficult</dt>
			<dd v-if="exercice.difficulty">{{ exercice.difficulty.value }}</dd>
			<dt v-if="exercice.exerciceIntensite">Intensit</dt>
			<dd v-if="exercice.exerciceIntensite">{{ exercice.exerciceIntensite.value }}</dd>
			<dt v-if="exercice.repetitionsMinimum">Rptitions minimum</dt>
			<dd v-if="exercice.repetitionsMinimum">{{ exercice.repetitionsMinimum }}</dd>
			<dt v-if="exercice.repetitionsMaximum">Rptitions maximum</dt>
			<dd v-if="exercice.repetitionsMaximum">{{ exercice.repetitionsMaximum }}</dd>
		</dl>
	</section>

</section>
</template>

<script>
import VueMarkdown from 'vue-markdown';

export default {
	name: 'exercice',
	components: {
		VueMarkdown,
	},
	data() {
		return {
			exercice: [],
      apiUrl: process.env.VUE_APP_CRAFT_API_BASE_URL,
		};
	},
	computed: {
		exerciceId() {
			return this.$route.params.id;
		},
	},
	mounted() {
		fetch(`${this.apiUrl}exercices/${this.exerciceId}.json`)
			.then((res) => {
				if (res.ok) return res.json();
				throw new Error('api data preload failed');
			})
			.then((res) => { this.exercice = res; })
			.catch(e => console.error(e.message));
	},
	method: {
		goBack() {
			window.history.back();
		},
	},
};
</script>

<style>
dl {}
dt {
    float: left;
    clear: left;
    width: 100px;
    text-align: right;
    font-weight: bold;
}
dt::after {
    content: ":";
}
dd {
    margin: 0 0 0 110px;
    padding: 0 0 0.5em 0;
}
  </style>
