<template>
  <v-layout row wrap align-center class="white--text">

    <v-flex xs12 md8 offset-md2 lg6 offset-lg3>
      <h1 class="mb-3">Settings</h1>
      <h2 class="mb-3">AirTable</h2>
      <v-card>
        <v-list three-line subheader>
          <v-list-tile>
            <v-text-field label="Table id" v-model="settings.atApi.table" hint="For example : appf1awfuQo3mxIqH" persistent-hint></v-text-field>
          </v-list-tile>
          <v-list-tile>
            <v-text-field label="AirTable key" v-model="settings.atApi.key" hint="For example : keyVCtqpVgfaRmJ3W" persistent-hint></v-text-field>
          </v-list-tile>
          <v-divider></v-divider>
          <v-list-tile>
            <v-list-tile-content>
              <v-list-tile-title>Base API url</v-list-tile-title>
              <v-list-tile-sub-title>{{settings.atApi.url}}{{settings.atApi.table}}?api_key={{settings.atApi.key}}</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
	name: 'Settings',
	data() {
		return {
			settings: this.$store.settingsStorage.fetch(),
		};
	},
	watch: {
		/* eslint-disable */
    settings: {
      handler: function () {
        this.$store.settingsStorage.save(this.settings);
        // console.log(this.$store.settingsStorage.fetch());
      },
      /* eslint-enable */
			deep: true,
		},
	},
};
</script>
