<template>
  <div>
    <section class="uk-section uk-padding-remove uk-section-primary uk-text-center uk-flex uk-flex-center uk-flex-middle" uk-height-viewport="offset-top: true; expand: true">
      <div class="uk-grid-match uk-grid-collapse uk-child-width-1-1" uk-grid>
        <div v-for="group in groups">
          <div class="uk-grid-match uk-grid-collapse uk-child-width-1-3@s uk-flex-center uk-flex-middle uk-text-center  uk-margin-small-top uk-margin-small-bottom" uk-grid>
            <div class="">
              <div class="uk-card uk-card-small uk-card-body">
                <div class="uk-h1 uk-margin-remove"><router-link :to="'/groups/'+group.id">{{group.title}}</router-link></div>
              </div>
            </div>
            <div v-if="group.nextSession">
              <div class="uk-card uk-card-small uk-card-body uk-card-secondary">
                <div class="">Prochain entranement</div>
                <div class="uk-h4 uk-margin-remove"><router-link :to="'/sessions/'+group.nextSession.id">Dans {{group.nextSession.fromNow|duration().locale('fr').humanize()}} </router-link></div>
                <div class="uk-text-meta">{{group.nextSession.schedule.date|moment().locale('fr').format('dddd DD')|capitalize}}  {{group.nextSession.schedule.date|moment().locale('fr').format('HH:mm')}}</div>
              </div>
            </div>
            <div v-else>
              <div class="uk-card uk-card-small uk-card-body uk-card-secondary">
                <div class="uk-h4"></div>
                <div class="uk-text-meta uk-text-success">Pas d'entranement programm prochainement</div>
              </div>
            </div>
            <div class="uk-visible@s" v-if="group.sessions">
              <div class="uk-card uk-card-small uk-card-body uk-text-left">
                <div class="uk-h4 uk-margin-remove">Prochaines sessions</div>
                <div v-for="s in group.sessions" class="uk-text-meta"><router-link :to="'/sessions/'+s.id">{{s.schedule.date|moment().locale('fr').format("dddd DD")|capitalize}}  {{s.schedule.date|moment().locale('fr').format('HH:mm')}}</router-link></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="uk-section uk-section-secondary uk-flex uk-flex-center uk-flex-middle uk-text-center uk-height-small">
      <div class="uk-h1"><router-link to="/drills">Programmes et drills</router-link></div>
    </section>
    <section class="uk-section uk-section-default uk-flex uk-flex-center uk-flex-middle uk-text-center uk-height-small">
      <div class="uk-h1"><a :href="backendAdminUrl">Backend</a></div>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue';

export default {
	name: 'home',
	components: {},
	data() {
		return {
			groups: [],
      backendAdminUrl: process.env.VUE_APP_CRAFT_ADMIN_URL,
      apiUrl: process.env.VUE_APP_CRAFT_API_BASE_URL,
		};
	},
	created() {
		// Call method fetchData when created
		this.fetchData();
	},
	methods: {
		fetchData() {
			// When online, fetch from api
			if (navigator.onLine) {
				this.groups = JSON.parse(localStorage.getItem('groups'));
				console.log('Online : groups fetched from localStorage');
				this.getGroups();
			} else {
				// When offline, fetch from cache
				this.groups = JSON.parse(localStorage.getItem('groups'));
				console.log('Offline : groups fetched from localStorage');
			}
		},
		getGroups() {
			/* eslint-disable */
      this.$Progress.start();
      this.$http.get(this.apiUrl+'groups.json')
      .then(response => {
        this.groups = response.data.data;
        console.log('Groups fetched from API');
        localStorage.setItem('groups', JSON.stringify(response.data.data));
        console.log('Groups sended to localStorage');
        this.$Progress.finish();
      }, response => {
        // error callback
      });
    }
  },
};
</script>
