<template>
  <section class="uk-section uk-container uk-container-small uk-width-xlarge">

    <h1>Programmes et drills</h1>
    <ul v-for="drill in drills" :key="drill.id">
      <li><router-link :to="'/drills/' + drill.id">{{ drill.title }}</router-link></li>
    </ul>

  </section>
</template>

<script>

export default {
	name: 'drills',
	data() {
		return {
			drills: [],
      apiUrl: process.env.VUE_APP_CRAFT_API_BASE_URL,
		};
	},
	created() {
		// Call method getData when mounted
		this.fetchData();
		// Call method getData every minute
		setInterval(() => {
			this.getData();
			console.log('Data fetched (every minute)');
		}, 360000);
	},
	methods: {
		fetchData() {
			// When online, fetch from api
			if (navigator.onLine) {
				this.quotes = JSON.parse(localStorage.getItem('drills'));
				console.log('Online : data fetched from localStorage');
				this.getSessions();
			} else {
				// When offline, fetch from cache
				this.quotes = JSON.parse(localStorage.getItem('drills'));
				console.log('Offline : data fetched from localStorage');
			}
		},
		getSessions() {
			/* eslint-disable */
      this.$http.get(`${this.apiUrl}drills.json`)
      .then(response => {
        this.drills = response.data.data;
        console.log('Drills fetched from API');
        localStorage.setItem('drills', JSON.stringify(response.data.data));
        console.log('Drills send to localStorage');
      }, response => {
        // error callback
      });
    }
  },
  computed: {},
};
</script>

