<template>
  <section class="uk-section uk-container uk-container-small uk-width-xlarge">

    <h1>Groupes</h1>
    <ul v-for="group in groups" :key="group.id">
      <li><router-link :to="'/groups/' + group.id">{{ group.title }}</router-link></li>
    </ul>

  </section>
</template>

<script>

export default {
	name: 'groups',
	data() {
		return {
			groups: [],
      apiUrl: process.env.VUE_APP_CRAFT_API_BASE_URL,
		};
	},
	created() {
		// Call method getData when mounted
		this.fetchData();
		// Call method getData every minute
		setInterval(() => {
			this.getData();
			console.log('Data fetched (every minute)');
		}, 360000);
	},
	methods: {
		fetchData() {
			// When online, fetch from api
			if (navigator.onLine) {
				this.quotes = JSON.parse(localStorage.getItem('groups'));
				console.log('Online : data fetched from localStorage');
				this.getGroups();
			} else {
				// When offline, fetch from cache
				this.quotes = JSON.parse(localStorage.getItem('groups'));
				console.log('Offline : data fetched from localStorage');
			}
		},
		getGroups() {
			/* eslint-disable */
      this.$http.get(`${this.apiUrl}groups.json`)
      .then(response => {
        this.groups = response.data.data;
        console.log('Groups fetched from API');
        localStorage.setItem('groups', JSON.stringify(response.data.data));
        console.log('Groups send to localStorage');
      }, response => {
        // error callback
      });
    }
  },
  computed: {},
};
</script>

