<template>
  <section class="">

    <section class="uk-section uk-section-small uk-container uk-container-small uk-width-xlarge pr-session-header">

      <span class="pr-no-print" onclick="window.print()"><i class="fas fa-print"></i></span>

      <div class="pr-meta">2018  Michael Ravedoni</div>


      <section v-if="session.type == 'session'" class="uk-section uk-section-xsmall uk-container uk-container-small uk-width-xlarge pr-session">

        <div class="uk-h4 uk-margin-remove"><router-link :to="'/groups/'+session.group.id">{{session.group.title}}</router-link></div>
        <h1 class="uk-h2 uk-margin-remove">{{session.schedule.date|moment().format('dddd DD MMMM YYYY') }}  {{session.schedule.date|moment().format('HH:mm') }}</h1>

        <div v-for="block in session.blocks" class="pr-block">
          <div class="pr-block-informations">
            <span v-if="block.heading" class="pr-block-heading">{{ block.heading }}</span>
            <span v-if="block.time" class="pr-block-time">{{ block.time }}'</span>
            <span v-if="block.rest" class="pr-block-rest">[{{ block.rest }}']</span>
          </div>
          <div v-if="block.text">{{ block.text }}</div>
          <div class="pr-block-exercices">
            <div v-for="ex in block.exercices" class="pr-block-exercice">

              <span v-if="ex.exerciceRelation" class="pr-block-exercice-link"><router-link class="uk-text-meta uk-text-small" :to="'/exercices/'+ex.exerciceRelation.id" target="_blank"><i class="far fa-question-circle"></i></router-link></span>
              <span v-if="ex.exerciceRelation" class="pr-block-exercice-heading">{{ ex.exerciceRelation.title }}</span>


              <span v-if="ex.exercice" class="pr-block-exercice-link"></span>
              <span v-if="ex.exercice" class="pr-block-exercice-heading">{{ ex.exercice }}</span>

              <span class="pr-block-exercice-duration"><span v-if="ex.series">{{ ex.series }}x</span><span v-if="ex.repetitions">{{ ex.repetitions }}x</span><span v-if="ex.series">{{ ex.series }}x</span><span v-if="ex.series == '' && ex.repetitions">x</span> <span v-if="ex.rest">[{{ ex.rest }}]</span></span>
              <span v-if="ex.note" class="pr-block-exercice-note">{{ ex.note }}</span>
            </div>
          </div>
        </div>

      </section><!-- end session -->

      <section v-if="session.type == 'cadre'" class="uk-section uk-section-xsmall uk-container pr-session">
        <div class="session-blocks-horizontal" uk-grid>

          <div v-for="block in session.blocks" class="uk-width-1-4@s pr-block">
            <div class="pr-block-informations">
              <span v-if="block.heading" class="pr-block-heading">{{ block.heading }}</span>
              <span v-if="block.time" class="pr-block-time">{{ block.time }}'</span>
              <span v-if="block.rest" class="pr-block-rest">[{{ block.rest }}']</span>
            </div>
            <div v-if="block.text">{{ block.text }}</div>
            <div class="pr-block-exercices">
              <div v-for="ex in block.exercices" class="pr-block-exercice">

                <span v-if="ex.exerciceRelation" class="pr-block-exercice-link"><router-link class="uk-text-meta uk-text-small" :to="'/exercices/'+ex.exerciceRelation.id" target="_blank"><i class="far fa-question-circle"></i></router-link></span>
                <span v-if="ex.exerciceRelation" class="pr-block-exercice-heading">{{ ex.exerciceRelation.title }}</span>


                <span v-if="ex.exercice" class="pr-block-exercice-link"></span>
                <span v-if="ex.exercice" class="pr-block-exercice-heading">{{ ex.exercice }}</span>

                <span class="pr-block-exercice-duration"><span v-if="ex.series">{{ ex.series }}x</span><span v-if="ex.repetitions">{{ ex.repetitions }}x</span><span v-if="ex.series">{{ ex.series }}x</span><span v-if="ex.series == '' && ex.repetitions">x</span> <span v-if="ex.rest">[{{ ex.rest }}]</span></span>
                <span v-if="ex.note" class="pr-block-exercice-note">{{ ex.note }}</span>
              </div>
            </div>
          </div>

        </div>
      </section><!-- end cadre -->

    </section>
  </section>
</template>

<script>
export default {
	name: 'session',
	data() {
		return {
			session: [],
      apiUrl: process.env.VUE_APP_CRAFT_API_BASE_URL,
		};
	},
	computed: {
		sessionId() {
			return this.$route.params.id;
		},
	},
	mounted() {
		if (navigator.onLine) {
			// When online, fetch data and set to localStorage
      this.$Progress.start();
			fetch(`${this.apiUrl}sessions/${this.sessionId}.json`)
				.then((res) => {
					if (res.ok) return res.json();
					throw new Error('api data preload failed');
				})
				.then((res) => {
					this.session = res;
					localStorage.setItem(`session-${this.sessionId}`, JSON.stringify(res));
					console.log('Online : session sended to localStorage');
          this.$Progress.finish();
				})
				.catch(e => console.error(e.message));
		} else {
			// When offline, fetch from localStorage
			this.session = JSON.parse(localStorage.getItem(`session-${this.sessionId}`));
			console.log('Offline : session fetched from localStorage');
		}
	},
};
</script>

